import React from "react";
import Box from "@mui/material/Box";
import Tab from "@mui/material/Tab";
import TabContext from "@mui/lab/TabContext";
import TabList from "@mui/lab/TabList";
import TabPanel from "@mui/lab/TabPanel";
import EventBanner from "../assets/Events/events-banner.png";
import EventAvailableRoundedIcon from "@mui/icons-material/EventAvailableRounded";
import CurrencyRupeeRoundedIcon from '@mui/icons-material/CurrencyRupeeRounded';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { ThemeProvider } from "@mui/material/styles";
import { Helmet } from "react-helmet";
import { theme } from "../components/theme";
import Typography from "@mui/material/Typography";
import { StyledCharacter } from "../components/styledCharacter/StyledCharacter";
import EventOne from "../assets/Events/event-one.png";
import { Card } from "@mui/material";
import "./style/events/event-one.scss";

const Events = () => {
  const [value, setValue] = React.useState("1");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div>
      <Helmet>
      <meta charSet="utf-8" />
        <title>Events | Velocita Racing</title>
      </Helmet>
      <ThemeProvider theme={theme}>
        <section id="section-zero-events">
          <div className="main-container">
            <div className="event-banner">
              <img src={EventBanner} alt="" />
              <div className="bottom-box">
                <div className="banner-event-details">
                  <div className="data-details">
                    <EventAvailableRoundedIcon className="event-icon" />
                    <div className="event-titles">
                      <span className="banner-event-title">VRF3 Unveil</span>
                      <span className="banner-event-subtitle">Coming Soon</span>
                    </div>
                    <div className="data-location">
                      <LocationOnIcon className="event-icon" />
                      <div className="event-titles">
                        <span className="banner-event-title">Ramaiah Institute of Technolgy</span>
                        <span className="banner-event-subtitle">
                          Bangalore
                        </span>
                      </div>
                    </div>
                    <div className="data-location">
                      <CurrencyRupeeRoundedIcon className="event-icon" />
                      <div className="event-titles">
                        <span className="banner-event-title">NA</span>
                        <span className="banner-event-subtitle">
                          
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="section-one-events">
          <div className="main-container">
            <Typography variant="h4" className="section-title">
              Explore Events
            </Typography>
            <div className="underline"></div>
            <Box className="box" sx={{ width: "100%", typography: "body1" }}>
              <TabContext value={value}>
                <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
                  <TabList className='color-tabs'
                    onChange={handleChange}
                    aria-label="lab API tabs example"
                  >
                    <Tab className="tab" label="Upcoming" value="1" />
                    <Tab className="tab" label="Workshops" value="2" />
                    <Tab className="tab" label="Games, Quiz and More" value="3" />
                  </TabList>
                </Box>
                <TabPanel className='tab-panel' value="1">
                  <Card className="event-card">
                    <div className="card-top">
                      <img src={EventOne} alt="" />
                    </div>
                    <div className="card-bottom">
                      <EventAvailableRoundedIcon className="event-icon" />
                      <div className="event-titles">
                        <span className="banner-event-title">VRF3 Unveil</span>
                        <span className="banner-event-subtitle">
                          Coming Soon
                        </span>
                      </div>
                    </div>
                  </Card>
                  <Card className="event-card">
                    <div className="card-top">
                      <img src={EventOne} alt="" />
                    </div>
                    <div className="card-bottom">
                      <EventAvailableRoundedIcon className="event-icon" />
                      <div className="event-titles">
                        <span className="banner-event-title">VRF3 Unveil</span>
                        <span className="banner-event-subtitle">
                          Coming Soon
                        </span>
                      </div>
                    </div>
                  </Card>
                </TabPanel>
                <TabPanel value="2">Item Two</TabPanel>
                <TabPanel value="3">Item Three</TabPanel>
              </TabContext>
            </Box>
          </div>
        </section>
      </ThemeProvider>
    </div>
  );
};

export default Events;
