import React from "react";
import { NavLink } from "react-router-dom";
import Typography from "@mui/material/Typography";
import { ThemeProvider } from "@mui/material/styles";
import { theme } from "../components/theme";
import Garrett from "../assets/Partners/garrett-full.png";

import "./style/sponsordetails/sponsordetails.scss";

const SponsorDetails = () => {
  return (
    <div>
      <ThemeProvider theme={theme}>
        <section id="sponsor-details-one">
          <div className="main-container">
            <div className="sponsor-title">
              <img src={Garrett} alt="" />
              <Typography className="title-content" variant="h5">
                Garrett Motion Inc.
              </Typography>
            </div>
            <div className="sponsor-about">
              <Typography className="about-content" variant="h6">
                {" "}
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Minus
                repellendus doloribus repellat excepturi! Illum earum sapiente
                sed eum, voluptas atque?
              </Typography>
            </div>
          </div>
        </section>

        {/* <section id='sponsor-details-two'>
            <div className="main-container">
                
            </div>
        </section> */}
      </ThemeProvider>
    </div>
  );
};

export default SponsorDetails;
